<template>
    <div class="wrap">
        <div class="webglDom" ref="webglDom"></div>
    </div>
</template>

<script>
import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"
import { TransformControls } from 'three/examples/jsm/controls/TransformControls';

import TWEEN from "tween.js";
const ThreeBSP = require('three-js-csg')(THREE)

export default {
    name: 'RunBox',
    data(){
        return {
            width: 0,
            height: 0,
            scene: null,
            camera: null,
            controls: null,
            renderer: null,
            isOpenDoor: true,
            amdLight: null,
            dirLight: null,
            ponLight: null,
            formControls: null,
            line: null,
            // 
            raycaster: '',
            mouse: '',
            dummy: '',

            curve: null,
            loopTime: 10 * 1000,
            runBox: null,
            cubeList: [],
        }
    },
    methods: {
        // 
        initScene(){
            // 
            const container = this.$refs.webglDom;
            this.width = container.offsetWidth;
            this.height = container.offsetHeight;
            this.scene = new THREE.Scene();
            this.scene.fog = new THREE.Fog(0x005577, 1, 2800)
            this.scene.background = new THREE.Color( 0x000000 );
            this.raycaster = new THREE.Raycaster();
            this.mouse = new THREE.Vector2();
            this.dummy = new THREE.Object3D();
        },
        // 
        initCamera(){
            this.camera = new THREE.PerspectiveCamera(45, this.width / this.height, 0.01, 10000);
            this.camera.position.set(0, 0, 800);
            this.add(this.camera);
            this.camera.lookAt(this.scene.position);
        },
        // 
        initControls(){
            this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        },
        // 
        initLight(){
            this.amdLight = new THREE.AmbientLight('#aaa');
			this.dirLight = new THREE.DirectionalLight('#aaa')
			this.ponLight = new THREE.PointLight('#aaa')
			this.amdLight.position.set( 0, 0, 0 )
			this.dirLight.position.set( 0, 0, 0 )
            this.ponLight.position.set( 0, 0, 0 )
			this.add(this.amdLight);		    // 
			this.add(this.dirLight);		    // 
			this.add(this.ponLight);			// 
        },
        // 
        initRenderer(){
            this.renderer = new THREE.WebGLRenderer({
                antialias: true,
                alpha: true,
            })
            this.renderer.setClearColor(0x4682B4,1.0);
            this.renderer.setClearAlpha(0);
            this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
            this.renderer.setSize(this.width, this.height);
            // 
            this.renderer.shadowMap.enabled = true;
            // 
            this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
            this.$refs.webglDom.appendChild(this.renderer.domElement);

            // 
            this.formControls = new TransformControls(this.camera, this.renderer.domElement)
            const mouse = new THREE.Vector2();
            this.renderer.domElement.addEventListener('click', (event) => {
                mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
                mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
            }, false);

            // 
            const rayCaster = new THREE.Raycaster();
            rayCaster.setFromCamera(mouse, this.camera);
            const intersects = rayCaster.intersectObjects(this.cubeList);
            console.log(intersects)
            if (intersects.length) {
                intersects.forEach(v => {
                    this.formControls.attach(v.object); // controls
                    this.add(this.formControls);
                })
                // const target = intersects[0].object;
            }

            // 
            this.formControls.addEventListener('dragging-changed', (event) => {
                if (!event.value) {
                    const points = this.curve.getPoints(50);
                    this.line.geometry.setFromPoints(points);
                }
            });

            this.render();
        },
        // 
        initScreen(){
            const initialPoints = [
                { x: 100, y: 50, z: -100 },
                { x: 100, y: -50, z: 100 },
                { x: -100, y: 0, z: 100 },
                { x: -100, y: 0, z: -100 }
            ];
            this.cubeList = initialPoints.map(v => {
                return this.addBox(10, 10, 10, '#fff', v.x, v.y, v.z);
            });
            this.curve = new THREE.CatmullRomCurve3(
                this.cubeList.map((cube) => cube.position) // position
            );
            this.curve.curveType = 'chordal'; // 
            this.curve.closed = true; // 
            const points = this.curve.getPoints(50); // 50
            this.line = new THREE.LineLoop(
                new THREE.BufferGeometry().setFromPoints(points),
                new THREE.LineBasicMaterial({ color: 0x00ff00 })
            ); // 

            this.runBox = this.addBox(10, 10, 10, '#fff', 0, 0, 0)
            this.add(this.line);
        },
        changePosition (t) {
            const position = this.curve.getPointAt(t); // t: 
            this.runBox.position.copy(position);
        },
        changeLookAt (t) {
            const tangent = this.curve.getTangentAt(t);
            const lookAtVec = tangent.add(this.runBox.position); // 
            this.runBox.lookAt(lookAtVec);
        },
        // 
        addBox (chang, gao, kuan, color, x, y, z) {
            const geometry = new THREE.BoxGeometry(chang, gao, kuan);
            const material = new THREE.MeshLambertMaterial ({
                color: color
            });
            const mesh = new THREE.Mesh(geometry, material);
            mesh.position.set(x, y, z);
            this.add(mesh);
            return mesh
        },
        // 
        add (obj) {
            this.scene.add(obj);
        },
        render () {
            let time = Date.now();
            let t = (time % this.loopTime) / this.loopTime; // 
            this.changePosition(t);
            this.changeLookAt(t);
            //  
            let vector = this.camera.position.clone();
            this.ponLight.position.set(vector.x,vector.y,vector.z); //
            this.renderer.render(this.scene, this.camera);
            TWEEN.update();
            requestAnimationFrame(this.render);
        },
        init () {
            this.initScene()
            this.initCamera()
            this.initLight()
            this.initScreen()
            this.initRenderer()
            this.initControls()
        }
    },
    mounted(){
        this.$nextTick(this.init);
    }
}
</script>

<style lang="scss">
    .wrap {
        width: 100%;
        height: 100%;
        background-size: 100% 100%;
        display: flex;
        .webglDom {
            width: 80%;
            height: 100%;
            // background: url('../assets/img/7.jpg') no-repeat;
            background-size: 100% 100%;
            canvas {
                width: 100%;
            }
        }
    }
</style>


